import { Component } from '@angular/core';
import { AlertController,IonicPage, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-categoriaforms-list',
  templateUrl: 'categoriaforms-list.html',
})

export class CategoriaformsListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
idempresa:any;
rol:any;
  nivelprx:any;
  username: string = "";
 
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  tiposus: any=[];
  public idsuscriptor                : any;

  constructor(public storage: Storage,public alertCtrl: AlertController, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    public navParams: NavParams,
     public regService: Auth) {

      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
      });
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    
  }
  showFilter()     
  {}

  pideregistros() { 
    let self = this;    self.loading = true;

    switch (this.nivelprx) {
      case '?key=1'://relacionar usuarios con categorias
            
             this.regService.getReg('grupo/'+ 'categoria-formmovil/'+this.idempresa,'frmcats').then((data) => {
             this.items=data
             self.loading = false;
              }, (err) => {
                this.msj.showError3('Error',err.error);
              });

      break;
      case '?key=2'://crear FORMULARIO  AQUI SE FILTRA
        
          this.regService.getReg( this.idsuscriptor+'/'+ 'categoriausr/' + this.idempresa,'catusuarios').then((data) => {
          this.items=data
          self.loading = false;
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
    
      
      break;   
      case '?key=3'://
      var queryx=''
      if(this.rol=='lluviaideas' || this.rol=='RECTOR')
      {
        queryx='5cdec4929312042f589bf0b4'+'/'+ 'categoriausr222/5cdec5279312042f589bf0b8'     
      }
      else{
    queryx='5cdec4929312042f589bf0b4'+'/'+ 'categoriausr22/' + this.idempresa
      }

        
      this.regService.getReg(queryx ,'catusuarios').then((data) => {
       this.items=data
      self.loading = false;
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });

   
  
  break;
  case '?key=4'://crear FORMULARIO  AQUI SE FILTRA


        
  this.regService.getReg( '5cdec4929312042f589bf0b4'+'/'+ 'categoriausr22/' + this.idempresa,'catusuarios').then((data) => {
   this.items=data
  self.loading = false;
}, (err) => {  
this.msj.showError3('Error',err.error);
});



break;
      default:
      self.loading = false;
        }


  }
  ionViewDidLoad() {

    this.nivelprx = this.navParams.get("nivel");
    //?key=1
    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');
      this.idsuscriptor=this.tiposus[3]
      this.rol=this.tiposus[6]

     // this.username = value;
      this.pideregistros();
  }); 
   
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }
   
  updateRecord(item : any,no) : void
  {

    switch (this.nivelprx) {
      case '?key=1'://relacionar usuarios con categorias
      this.navCtrl.push('CategoriaformsusrListPage', { record : item });
          break;
      case '?key=2'://crear
      this.navCtrl.push('FormularioMovilListPage', { record : item,record2:{idusuario:this.idsuscriptor} });
          break;
          case '?key=3'://crear
          this.navCtrl.push('MarketmailListPage', { record : item,idsuscriptor:this.idsuscriptor,idrol:this.rol,titulo:'Dise√±o de plantillas',record2:{idusuario:this.idsuscriptor},idempresa:this.idempresa,idcategoria:item._id });
              break;
              case '?key=4'://crear
              this.navCtrl.push('MailgrupoListPage', { record : item,idrol:this.rol,titulo:item.nombre,record2:{idusuario:this.idsuscriptor},idempresa:this.idempresa,idcategoria:item._id });
                  break;
      default:

  }

    
    
  }



}



